import { Brand, BrandContextType } from "../BrandContext/BrandContext";

export const featureToggles = (ctx: BrandContextType, toggle: string) =>
    ctx.featureToggles && ctx.featureToggles.find(fToggle => fToggle === toggle)

export const verifyIsBrand = (ctx, brandName) => ctx.brand === Brand[brandName]

export const setCookie = ({ prefix, value, expiresInDay, name }) => {
    const date = new Date();
    date.setTime(date.getTime() + expiresInDay * 24 * 60 * 60 * 1000);
    const expires = `expires=${date.toUTCString()}`;
    document.cookie = `${prefix + name}=${value};${expires};path=/`;
}

export const getCookie = ({ name }) => {
    const key = `${name}=`;
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i += 1) {
        let cookie = cookies[i];
        while (cookie.charAt(0) === " ") {
            cookie = cookie.substring(1);
        }
        if (cookie.indexOf(key) === 0) {
            return cookie.substring(key.length, cookie.length);
        }
    }
    return "";
}

export const getParam = (param) => {
    const url: any = new URL(window.location.href);
    return url?.searchParams.get(param);
}

export const findCognitoIdToken = () => {
    for (let i = 0; i < localStorage.length; i += 1) {
        const key: any = localStorage.key(i);
        if (key.startsWith("CognitoIdentityServiceProvider") && key.endsWith("idToken")) {
            return localStorage.getItem(key);
        }
    }

    return undefined;
}
